init();
animate();

// Adjusts the three.js standard shader to include batchid highlight
function batchIdHighlightShaderMixin(shader) {

	const newShader = { ...shader };
	newShader.uniforms = {
		highlightedBatchId: { value: - 1 },
		highlightColor: { value: new Color(0xFFC107).convertSRGBToLinear() },
		...UniformsUtils.clone(shader.uniforms),
	};
	newShader.extensions = {
		derivatives: true,
	};
	newShader.lights = true;
	newShader.vertexShader =
		`
			attribute float _batchid;
			varying float batchid;
		` +
		newShader.vertexShader.replace(
			/#include <uv_vertex>/,
			`
			#include <uv_vertex>
			batchid = _batchid;
			`
		);
	newShader.fragmentShader =
		`
			varying float batchid;
			uniform float highlightedBatchId;
			uniform vec3 highlightColor;
		` +
		newShader.fragmentShader.replace(
			/vec4 diffuseColor = vec4\( diffuse, opacity \);/,
			`
			vec4 diffuseColor =
				abs( batchid - highlightedBatchId ) < 0.5 ?
				vec4( highlightColor, opacity ) :
				vec4( diffuse, opacity );
			`
		);

	return newShader;

}

function init() {

	infoEl = document.getElementById('info');

	scene = new Scene();

	// primary camera view
	renderer = new WebGLRenderer({ antialias: true });
	renderer.setPixelRatio(window.devicePixelRatio);
	renderer.setSize(window.innerWidth, window.innerHeight);
	renderer.setClearColor(0x151c1f);
	renderer.shadowMap.enabled = true;
	renderer.shadowMap.type = PCFSoftShadowMap;
	renderer.outputEncoding = sRGBEncoding;

	document.body.appendChild(renderer.domElement);

	camera = new PerspectiveCamera(60, window.innerWidth / window.innerHeight, 1, 4000);
	camera.position.set(11.104438973861951, -11.39689213550978, 4.1864248184999795);

	// controls
	controls = new OrbitControls(camera, renderer.domElement);
	// controls.screenSpacePanning = false;
	// controls.minDistance = 1;
	// controls.maxDistance = 2000;
	controls.target.set(11.171061009948385, -7.839181, 5.32320420634048);

	// lights
	dirLight = new DirectionalLight(0xffffff, 1.25);
	dirLight.position.set(1, 2, 3).multiplyScalar(40);
	dirLight.castShadow = true;
	dirLight.shadow.bias = - 0.01;
	dirLight.shadow.mapSize.setScalar(2048);

	const shadowCam = dirLight.shadow.camera;
	shadowCam.left = - 200;
	shadowCam.bottom = - 200;
	shadowCam.right = 200;
	shadowCam.top = 200;
	shadowCam.updateProjectionMatrix();

	scene.add(dirLight);

	const ambLight = new AmbientLight(0xffffff, 0.5);
	scene.add(ambLight);

	offsetGroup = new Group();
	scene.add(offsetGroup);

	const windowHost = window.location.host;
	new B3DMLoader()
		.load("http://" + windowHost + '/example/isaac-data/run.b3dm')
		.then(res => (offsetGroup.add(res.scene)));



	// new B3DMLoader()
	// 	.load("http://" + windowHost + '/example/isaac-data/sci-cam-texture/run-cropped-opposite.b3dm')
	// 	.then(res => (offsetGroup.add(res.scene)));

	raycaster = new Raycaster();
	mouse = new Vector2();

	onWindowResize();
	window.addEventListener('resize', onWindowResize, false);

	renderer.domElement.addEventListener('click', (e) => {
		console.log({
			"camera.position": camera.position,
			"controls.target": controls.target
		})
	}, false);

	const gui = new GUI()
	// const cubeFolder = gui.addFolder('Cube')
	// cubeFolder.add(cube.rotation, 'x', 0, Math.PI * 2)
	// cubeFolder.add(cube.rotation, 'y', 0, Math.PI * 2)
	// cubeFolder.add(cube.rotation, 'z', 0, Math.PI * 2)
	// cubeFolder.open()
	const cameraFolder = gui.addFolder('Camera')
	cameraFolder.add(camera.position, 'z', 0, 10)
	cameraFolder.open()

	let hdWall = null;

	let objRemove = {
		remove: function () {
			if (hdWall) {
				offsetGroup.remove(hdWall)
			}
		}
	}

	let obj = {
		add: function () {
			new B3DMLoader()
			.load("http://" + windowHost + '/example/isaac-data/sci-cam-texture/run-cropped.b3dm')
			.then(res => {
				
				hdWall = res.scene;
				offsetGroup.add(hdWall);
				
			});
		}
	};

	gui.add(obj, 'add');
	gui.add(objRemove, 'remove');



}


function onWindowResize() {

	camera.aspect = window.innerWidth / window.innerHeight;
	renderer.setPixelRatio(window.devicePixelRatio);
	renderer.setSize(window.innerWidth, window.innerHeight);
	camera.updateProjectionMatrix();

}

function animate() {

	requestAnimationFrame(animate);

	render();


}

function render() {

	renderer.render(scene, camera);

}
